<template>
  <div class="vue-ui-group">
    <slot />
  </div>
</template>

<script>
export default {
  name: 'VueGroup',

  model: {
    event: 'update',
  },

  provide() {
    return {
      VueGroup: {
        data: this.injection,
        setValue: this.setValue,
      },
    };
  },

  props: {
    value: {},
  },

  data() {
    return {
      injection: {
        value: this.value,
      },
    };
  },

  watch: {
    value(value, oldValue) {
      if (value !== oldValue) {
        this.injection.value = value;
      }
    },
  },

  methods: {
    setValue(value) {
      this.$emit('update', value);
    },
  },
};
</script>

