<template>
  <VueButton
    class="vue-ui-group-button"
    :class="{
      selected,
      flat: flat && !selected
    }"
    :aria-selected="selected"
    v-bind="$attrs"
    @click="handleClick"
  >
    <slot />
  </VueButton>
</template>

<script>
export default {
  name: 'VueGroupButton',

  inheritAttrs: false,

  inject: ['VueGroup'],

  props: {
    value: {},

    flat: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    selected() {
      return this.value === this.VueGroup.data.value;
    },
  },

  watch: {
    selected(value, oldValue) {
      if (value !== oldValue) {
        this.$emit('selected', value);
      }
    },
  },

  methods: {
    handleClick(...args) {
      this.$emit('click', ...args);
      this.VueGroup.setValue(this.value);
    },
  },
};
</script>

<style lang="scss">
@import '../style/imports';
.vue-ui-group-button.vue-ui-button {
  &:not(.selected):not(.flat) {
    @include button-colors($vue-ui-grey-800, lighten($vue-ui-grey-100, 9%));
  }
  border-color: transparent;
  .vue-ui-group & {
    &.selected {
      &:not(.black):not(.primary):not(.danger):not(.warning):not(.info):not(.success):not(.flat) {
        @include button-colors($vue-ui-white, #2d3444);
      }
    }
  }
  .vue-ui-group:not(.vertical) & {
    &:not(.flat) {
      &:not(.round) {
        &:not(:first-child) {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
        &:not(:last-child) {
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
      &.round {
        padding-left: 20px;
        padding-right: 20px;
        &.icon-button {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }
  .vue-ui-group.vertical & {
    display: flex;
    &:not(.icon-button) {
      width: 100%;
    }
    &:not(.flat) {
      &:not(:first-child) {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      &:not(:last-child) {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
    }
  }
}
</style>
