<template>
  <VueGroupButton
    ref="groupButton"
    class="vue-ui-select-button"
    v-bind="$attrs"
    v-on="$listeners"
    v-close-popover="!keepOpen"
    flat
    @selected="onSelect"
  >
    <slot/>
  </VueGroupButton>
</template>

<script>

export default {
  name: 'VueSelectButton',


  inject: [
    'VueSelect',
  ],

  props: {
    selectLabel: {
      type: String,
      default: null,
    },
    keepOpen: {
      type: Boolean,
      default: false,
    },
  },

  mounted() {
    this.onSelect(this.$refs.groupButton.selected);
  },

  methods: {
    onSelect(selected) {
      if (selected) {
        this.VueSelect.setCurrentChild(this);
      }
    },
  },
};
</script>

<style lang="scss">
@import "../style/imports";
.vue-ui-select-button {
  border-radius: 4px !important;
  justify-content: end;
}
</style>
