<template>
  <div
    class="vue-ui-form-field"
    :class="{
      focused: injectedData.focused,
      [`status-${injectedData.status}`]: injectedData.status
    }"
  >
    <div class="wrapper">
      <div class="title">
        <slot name="title">
          <span v-html="title" />
        </slot>
      </div>
      <div class="content">
        <slot />
      </div>
      <div
        class="subtitle"
        :class="{
          [`vue-ui-text ${injectedData.status}`]: injectedData.status
        }"
      >
        <VueIcon v-if="subtitleIconId" :icon="subtitleIconId" />
        <slot name="subtitle">
          <span v-html="subtitle" />
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
const statusIcons = {
  danger: 'error',
  warning: 'warning',
  info: 'info',
  success: 'check_circle',
};

export default {
  name: 'VueFormField',

  provide() {
    return {
      VueFormField: {
        data: this.injectedData,
      },
    };
  },

  props: {
    subtitle: {
      type: String,
      default: undefined,
    },

    subtitleIcon: {
      type: String,
      default: undefined,
    },

    statusIcon: {
      type: Boolean,
      default: false,
    },

    title: {
      type: String,
      default: undefined,
    },
  },

  data() {
    return {
      injectedData: {
        focused: false,
        status: null,
      },
    };
  },

  computed: {
    subtitleIconId() {
      if (this.subtitleIcon) {
        return this.subtitleIcon;
      }
      if (this.statusIcon) {
        const { status } = this.injectedData;
        if (status) {
          return statusIcons[status];
        }
        return false;
      }
      return false;
    },
  },
};
</script>

<style lang="scss">
@import "../style/imports";
.vue-ui-form-field {
  > .wrapper {
    &,
    > .content {
      @include v-box();
      align-items: stretch;
    }
    > .content {
      margin: 6px 0;
      > * {
        @include space-between-y(8px);
      }
    }
    > .title,
    > .subtitle {
      transition: opacity 0.3s;
    }
    > .subtitle {
      font-size: 14px;
      &:not(.black):not(.primary):not(.danger):not(.warning):not(.info):not(.success):not(.flat) {
        color: $vue-ui-grey-600;
        .vue-ui-icon {
            fill: $vue-ui-grey-600;
        }
      }
    }
  }
  &:not(.focused) {
    > .wrapper {
      > .title,
      > .subtitle {
        opacity: 0.75;
      }
    }
  }
}
</style>
