<template>
  <button
    class="vue-ui-button"
    :class="[
      {
        disabled: finalDisabled,
        loading,
        ghost,
        small,
      }
    ]"
    :aria-disabled="ghost"
    @click.capture="handleClick"
  >
    <VueLoading v-if="loading" class="inline loading-secondary" />
    <VueIcon v-else-if="iconLeft" :icon="iconLeft" class="button-icon left" />
    <slot>{{ label }}</slot>
    <VueIcon v-if="iconRight" :icon="iconRight" class="button-icon right" />
  </button>
</template>

<script>
import DisabledChild from '../mixins/DisabledChild';

export default {
  name: 'VueButton',

  inheritAttrs: false,

  mixins: [DisabledChild],

  props: {
    iconLeft: {
      default: null,
    },
    iconRight: {
      default: null,
    },
    label: {
      type: String,
      default: '',
    },
    small: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ghost() {
      return this.finalDisabled || this.loading;
    },
  },

  methods: {
    handleClick(event) {
      if (this.ghost) {
        event.preventDefault();
        event.stopPropagation();
        event.stopImmediatePropagation();
      } else {
        this.$emit('click', event);
      }
    },
  },
};
</script>

<style lang="scss">
@import "../style/imports";
@mixin button-colors($foreground, $background) {

  $secBackground: $background;
  @if $background == $vue-ui-white {
    color: $vue-ui-grey-800;
    background: $background;
    border-color: $vue-ui-grey-100;
  }
  @else if $background == lighten($vue-ui-grey-100, 9%) {
    $secBackground: lighten($vue-ui-grey-300, 8%);
    background: $background;
    border-color: $vue-ui-grey-100;
  }
  @else {
    color: $foreground;
    background: $background;
    border-color: $background;
  }
  &:not(.ghost) {
    &:hover {
      background: lighten($secBackground, 18%);
    }
    &:active {
      background: darken($background, 5%);
    }
  }
  .animation {
    border-right-color: $foreground;
  }
}
@mixin colors($dark, $light, $invert: false) {
  $foreground: "";
  $background: "";
  @if $invert {
    $foreground: $light;
    $background: $dark;
  } @else {
    $foreground: $dark;
    $background: $light;
  }
  @include button-colors($foreground, $background);
  &.flat {
    @include button-colors($dark, transparent);
    &:not(.ghost) {
      &:hover,
      &:active,
      .vue-ui-dropdown.open .dropdown-trigger & {
        @include button-colors($foreground, $background);
      }
    }
  }
}
.vue-ui-button {
  display: inline-flex;
  vertical-align: middle;
  cursor: pointer;
  outline: none;
  user-select: none;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0 14px;
  font-weight: 500;
  line-height: 16px;
  border: 1px solid transparent;
  height: 32px;
  align-items: center;
  justify-content: space-between;
  transition: background-color 0.2s, color 0.2s;
  &.small {
    height: 24px;
  }
  &.round {
    border-radius: 17px;
    &.small {
      border-radius: 12px;
    }
  }
  .vue-ui-icon{
    fill: currentColor;
  }
  &:not(.icon-button) {
    > .button-icon {
      position: relative;
      &.left {
        margin-right: 6px;
        left: -2px;
      }
      &.right {
        margin-left: 6px;
        left: 2px;
      }
    }
    > .loading-secondary {
      position: relative;
      margin-right: 6px;
      left: -2px;
    }
  }
  &.icon-button {
    padding: 0;
    width: 32px;
    height: 32px;
    justify-content: center;
    &.small {
      width: 24px;
      height: 24px;
    }
  }
  &.ghost {
    cursor: default;
  }
  &.disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }
  @include colors($vue-ui-grey-800, lighten($vue-ui-grey-100, 9%));
  &.black {
    @include colors(#2d3444, $vue-ui-white, true);
  }
  &.white {
    @include colors($vue-ui-white, $vue-ui-white, true);
  }
  &.purple {
    @include colors(#b35bb5, $vue-ui-white, true);
  }
  &.red {
    @include colors($vue-ui-danger-500, $vue-ui-white, true);
  }
  &.orange {
    @include colors($vue-ui-warning-500, $vue-ui-white, true);
  }
  &.blue {
    @include colors($vue-ui-info-500, $vue-ui-primary-100, true);
  }
  &.green {
    @include colors($vue-ui-primary-500, $vue-ui-primary-100, true);
  }
}
</style>
