const icons = require.context(
  '../node_modules/material-design-icons/sprites/svg-sprite',
  true,
  /svg-sprite-(\w+)-symbol\.svg$/,
);

const iconsWrapper = document.createElement('svg');
iconsWrapper.style.display = 'none';
iconsWrapper.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
iconsWrapper.setAttribute('xmlns:xlink', 'http://www.w3.org/1999/xlink');
iconsWrapper.setAttribute('aria-hidden', 'true');
iconsWrapper.setAttribute('id', '__SVG_SPRITE_NODE__');

icons.keys().forEach((key) => {
  const result = icons(key);
  iconsWrapper.innerHTML += result;
});

document.body.insertBefore(iconsWrapper, document.body.firstChild);
